.author {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  margin-right: 20px;
  min-height: 60px;
  &--company {
    .author__logo {
      height: 20px;
      border-radius: 0; }
    .author__logo-img {
      border-radius: 0; } } }

.author__logo {
  width: 60px;
  height: 60px;
  margin-right: 17px; }

.author__logo-img {
  width: 100%;
  height: 100%;
  border-radius: 50%; }

.author__info {
  font-size: 1.6rem;
  line-height: 1.3;
  font-style: italic;
  color: #8d8d8d; }
.author__name, .author__position {
  width: fit-content;
  display: block;
  position: relative;
  text-decoration: none;
  &::after {
    content: '';
    position: absolute;
    bottom: -1px;
    left: 0;
    width: 100%;
    border-bottom: 1px solid #f4f4f4;
    transition: 0.2s; } }


@media screen and (max-width: 576px) {
  .author {
    flex-wrap: wrap;
    &__logo {
      margin-bottom: 12px; }
    &__info {
      font-size: 1.4rem; } } }


/*animation*/
.author {
  &__info:hover {
    text-decoration: none;
    color: #8d8d8d;
    .author__name, .author__position {
      display: block;
      &::after {
        border-color: #8d8d8d;
        transition: 0.2s; } } }
  &__info:active {
    color: #717171;
    .author__name, .author__position {
      &::after {
        border-color: #717171; } } } }
